.ourService_heading {
    font-weight: 800;
    font-size: 32px;
    line-height: 125%;
    text-align: center;
    color: #111111;
    min-height: 105px;
}

.ourService_arrowButton {
    background-color: #ffffff;
    margin-right: 16px;
    margin-left: auto;
    width: 60px;
    height: 60px;
}

.ourService_arrowButton:hover {
    border: 1px solid #67AEFC;
    border-radius: 15px;
}

.ourService_arrowButton:active {
    border: none;
    border-radius: 15px;
    background-color: #67AEFC;
    color: #ffffff;
}

.ourService {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #DAECFF;
    min-height: 703px;
    position: relative;
}

.ourService_description {
    display: inline-block;
    margin-left: 28px;
}

.ourService_item {
    display: flex;
    align-items: center;
    min-width: 1169px;
    min-height: 61px;
    background-color: #ffffff;
    border-radius: 15px;
    padding-left: 44px;
    font-size: 18px;
    line-height: 175%;
}

.ourService_item:not(:last-child) {
    margin-bottom: 10px;
}

.ourService_item::before {
    content: url('../../images/beforeEllipse.png');
}

.bag {
    position: absolute;
    top: 463px;
    right: 145px;
}

/* ------------------------------------ Media rules 1440 - 1920 -------------------------------------------------*/
@media screen and (min-width:1440px) and (max-width:1919.5px) {
    .bag {
        right: 0;
        top: 553px;
        width: 123px;
        height: 138px;
    }
}

/* ------------------------------------ Media rules 1024 - 1440 -------------------------------------------------*/
@media screen and (min-width:300px) and (max-width:1439.5px) {
    .ourService_item {
        min-width: 969px;
    }

    .bag {
        display: none;
    }
}

/* ------------------------------------ Media rules 768 - 1024 -------------------------------------------------*/
@media screen and (min-width:768px) and (max-width:1023.5px) {
    .ourService_item {
        min-width: 669px;
    }
}

/* ------------------------------------ Media rules 320 - 768 -------------------------------------------------*/
@media screen and (min-width:320px) and (max-width:767.5px) {
    .ourService {
        display: block;
        padding-top: 16px;
        padding-bottom: 30px;
        padding-left: 20px;
        padding-right: 20px;
        min-height: 0;
    }

    .ourService_heading {
        font-weight: 800;
        font-size: 23px;
        line-height: 150%;
        text-align: center;
        margin-bottom: 17px;
    }

    .ourService_item {
        max-width: 335px;
        min-width: 0px;
        padding-left: 24px;
        font-size: 17px;
        line-height: 175%;
    }

    .ourService_item::before {
        content: url('../../images/mobile/listStyle.png');
    }
}